////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// layout mixins - start

@mixin eltdfRelativeHolderLayout($vertical-align: middle) {
    position: relative;
    display: inline-block;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin eltdfAbsoluteHolderLayout() {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

@mixin eltdfFlexBoxLayout($position: null, $align-items: null, $justify-content: null) {
    @if ($position) {
        position: $position;
    }
    
    @include eltdfFlexLayout();
    
    @if ($align-items) {
        @include eltdfFlexAlignItems($align-items);
    }
    
    @if ($justify-content) {
        @include eltdfFlexJustifyContent($justify-content);
    }
}

@mixin eltdfInlineFlexBoxLayout($position: null, $align-items: null, $justify-content: null) {
	@if ($position) {
		position: $position;
	}
	
	@include eltdfInlineFlexLayout();
	
	@if ($align-items) {
		@include eltdfFlexAlignItems($align-items);
	}
	
	@if ($justify-content) {
		@include eltdfFlexJustifyContent($justify-content);
	}
}

@mixin eltdfFlexContainer($align-items: null, $justify-content: null, $flex-direction: null, $flex-wrap: null, $align-content: null) {
	@include eltdfFlexBoxLayout(null, $align-items, $justify-content);

	@if ($flex-direction) {
		flex-direction: $flex-direction;
	}

	@if ($flex-wrap) {
		flex-wrap: $flex-wrap;
	}

	@if ($align-content) {
		align-content: $align-content;
	}
}

@mixin eltdfInlineFlexContainer($align-items: null, $justify-content: null, $flex-direction: null, $flex-wrap: null, $align-content: null) {
	@include eltdfInlineFlexBoxLayout(null, $align-items, $justify-content);
	
	@if ($flex-direction) {
		flex-direction: $flex-direction;
	}
	
	@if ($flex-wrap) {
		flex-wrap: $flex-wrap;
	}
	
	@if ($align-content) {
		align-content: $align-content;
	}
}

@mixin eltdfFlexLayout() {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
}

@mixin eltdfInlineFlexLayout() {
    display: -webkit-inline-flex;
    display: -ms-inline-flexbox;
    display: inline-flex;
}

@mixin eltdfFlexItem($order: 0, $flex-grow: 0, $flex-shrink: 1, $flex-basis: auto) {
    order: $order;
    flex-grow: $flex-grow;
    flex-shrink: $flex-shrink;
    flex-basis: $flex-basis;
}

@mixin eltdfFlexAlignItems($align-items) {
    $older-align-items: $align-items;
    
    @if ($align-items == 'flex-start') {
        $older-align-items: start;
    } @else if ($align-items == 'flex-end') {
        $older-align-items: end;
    }
    
    -webkit-box-align: $older-align-items;
    -webkit-align-items: $align-items;
    -ms-flex-align: $older-align-items;
    align-items: $align-items;
}

@mixin eltdfTableLayout() {
    position: relative;
    display: table;
    table-layout: fixed;
    height: 100%;
    width: 100%;
}

@mixin eltdfTableCellLayout($vertical-align: middle) {
    position: relative;
    display: table-cell;
    height: 100%;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin eltdfTypographyLayout() {
    color: inherit;
    font-family: inherit;
    font-size: inherit;
    font-weight: inherit;
    font-style: inherit;
    line-height: inherit;
    letter-spacing: inherit;
    text-transform: inherit;
}

// layout mixins - end

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// transition mixins - start

@mixin eltdfTransition($transition-param...) {
    -webkit-transition: $transition-param;
    -moz-transition: $transition-param;
    transition: $transition-param;
}

@mixin eltdfTransitionTransform($transition-param...) {
    -webkit-transition: -webkit-transform $transition-param;
    -moz-transition: -moz-transform $transition-param;
    transition: transform $transition-param;
}

@mixin eltdfTransform($transform-param...) {
    -webkit-transform: $transform-param;
    -moz-transform: $transform-param;
    transform: $transform-param;
}

@mixin eltdfAnimation($animation-param...) {
    -webkit-animation: $animation-param;
    -moz-animation: $animation-param;
    animation: $animation-param;
}

@mixin eltdfTransformOrigin($animation-param...) {
    -webkit-transform-origin: $animation-param;
    -moz-transform-origin: $animation-param;
    transform-origin: $animation-param;
}

//flicker fix mixin
@mixin eltdfFlickerFix() {
	will-change: transform;
	-webkit-filter: blur();
	filter: blur();
}

// transition mixins - end

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// checkbox mixins - start

$checkbox-size: 15px;
$checkbox-border-width: 1px;

%checkbox-style {
    position: relative;
    margin: 8px 0;
    line-height: 1;

    input[type=checkbox] {
        width: $checkbox-size;
        height: $checkbox-size;
        max-height: $checkbox-size;
        position: relative;
        display: inline-block;
        vertical-align: top;
        top: 0;
        left: 0;
        margin: 0;
    }

    input[type=checkbox] + label {
        position: absolute;
        top: 0;
        left: 0;
        display: inline-block;
        line-height: 0;
        pointer-events: none;
        cursor: pointer;
    }

    input[type=checkbox] + label span.eltdf-label-text {
        display: inline-block;
        padding-left: 10px;
        line-height: $checkbox-size;
        color: $default-heading-color;
    }

    input[type=checkbox] + label .eltdf-label-view {
        display: inline-block;
        vertical-align: top;
        width: $checkbox-size;
        height: $checkbox-size;
        background-color: $default-background-color;
        border: $checkbox-border-width solid $default-border-color;
        border-radius: 2px;
        cursor: pointer;
        box-sizing: border-box;

        &:hover {
            cursor: pointer;
        }
    }

    input[type=checkbox] + label .eltdf-label-view:after {
        content: '';
        position: absolute;
        top: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
        left: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
        width: $checkbox-size / 2 - $checkbox-border-width;
        height: $checkbox-size / 2 - $checkbox-border-width;
        background-color: $first-main-color;
        opacity: 0;
        @include eltdfTransition(opacity 0.3s ease-in-out);
    }

    input[type=checkbox]:checked + label .eltdf-label-view:after {
        opacity: 1;
    }
}

// checkbox mixins - end

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// common mixins - start

@mixin eltdfBckImageStyle() {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin eltdfImageOverlayHoverStyle($with-hover: true) {
    
    @if ($with-hover) {
        
        &:hover {
            
            &:after {
                opacity: 1;
            }
        }

        &:after {
            @include eltdfAbsoluteHolderLayout();
            content: '';
            background-color: rgba(#fff, .7);
            opacity: 0;
            @include eltdfTransition(opacity .2s ease-in-out);
        }

    } @else {
        @include eltdfAbsoluteHolderLayout();
        content: '';
        background-color: rgba(#fff, .7);
        opacity: 0;
        @include eltdfTransition(opacity .2s ease-in-out);
    }
}

@mixin eltdfImageMoveHoverStyle($scale: 1.06, $pixels: 3%, $hover-duration:.75s, $hover-out-duration:.45s) {
	
	overflow: hidden;
	@include eltdfFlickerFix();
	
	img {
		backface-visibility: hidden;
		transform-origin: top;
		@include eltdfTransform(scale($scale));
		@include eltdfTransitionTransform($hover-out-duration cubic-bezier(.33,.02,0,.93))
	}
	
    &:hover {
        img {
	        transform: scale($scale) translate3d(-$pixels,0,0);
	        @include eltdfTransitionTransform($hover-duration cubic-bezier(.33,.02,0,.93))
        }
    }
}

@mixin eltdfStandardPaginationStyle($list_type: null) {
    @include eltdfRelativeHolderLayout(top);
    margin: 40px 0 0;
    clear: both;
    font-family: $default-heading-font;
    font-size: 14px;
    font-weight: 500;

    ul {
        @include eltdfRelativeHolderLayout(top);
        padding: 0;
        margin: 0;
        list-style: none;
        text-align: left;

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;

            >a, >span{
                position: relative;
                display: inline-block;
                vertical-align: top;
                margin: 0 13px;
                padding: 0;
	
		            @include eltdfAnimationLineHidden($bottom:0, $color:currentColor, $width: calc(100% + 12px), $left: -6px);
		            
		            &:hover {
			            @include eltdfAnimationLineVisible($bottom:0, $color:currentColor, $width: calc(100% + 12px), $left: -6px);
		            }

                &.current {

                    &:after {
	                    content: '';
	                    position: absolute;
	                    display: block;
	                    height: 1px;
	                    width: calc(100% + 12px);
	                    left: -6px;
	                    right: auto;
	                    bottom: 0;
	                    background: $first-main-color;
	                    opacity: 1;
	                    @include eltdfTransition(opacity 0.15s ease-in-out);
                    }
                }
            }

            &:first-child{

                >a, >span{
                    margin-left: 0;
                }
            }

            &:last-child{

                >a, >span{
                    margin-right: 0;
                }
            }

            &.eltdf-pag-active {
                
                a {
                    color: $first-main-color;

                    &:after {
                        opacity: 1;
                        @include eltdfTransition(opacity 0.15s ease-in-out);
                    }
                }
            }

            &.eltdf-pag-prev,
            &.eltdf-pag-next,
            &.eltdf-pag-first,
            &.eltdf-pag-last {
                margin: 0 2px;

                a {
                    font-size: 24px;

                    span {
                        display: block;
                        line-height: inherit;

                        &:before {
                            display: block;
                            line-height: inherit;
                        }
                    }

                    &:before,
                    &:after {
                        content: none;
                        display: none;
                    }
                }
            }

            @if ($list_type == 'shortcode') {
                
                &.eltdf-pag-prev {
                    
                    a {
                        opacity: 0;
                    }
                }

                &.eltdf-pag-next {
                    
                    a {
                        opacity: 1;
                    }
                }

            } @else if ($list_type == 'shop') {

                a {
                    
                    &.next,
                    &.prev {
                        font-size: 0;
                        line-height: 0;

                        &:before {
                            display: block;
                            font-family: 'ElegantIcons'; // same icon pack as in our templates for pagination
                            font-size: 22px;
                            line-height: 23px;
                            -webkit-font-smoothing: antialiased;
                            -moz-osx-font-smoothing: grayscale;
                        }

                        &:after {
                            content: '';
                            display: none;
                        }
                    }

                    &.prev {
                        margin-right: 3px;

                        &:before {
                            content: "\34";
                        }
                    }

                    &.next {
                        margin-left: 3px;

                        &:before {
                            content: "\35";
                        }
                    }
                }
            }
        }
    }
}

@mixin eltdfButtonDefaultStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    margin: 0;
    font-family: $default-heading-font;
    font-size: 13px;
    line-height: 2.1em;
    font-weight: 600;
    outline: none;
    box-sizing: border-box;
    @include eltdfTransition(color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out);
}

@mixin eltdfButtonSize($size: medium) {
    
    @if ($size == 'small') {
        padding: 8px 36px;

    } @else if ($size == 'medium') {
        padding: 9px 46px;

    } @else if ($size == 'large') {
        padding: 13px 86px;

    } @else if ($size == 'huge') {
        display: block;
        width: 100%;
        text-align: center;
        padding: 13px 26px;
    }
}

@mixin eltdfAnimationLineHidden($left: 0, $bottom:4px, $width: 100%, $height: 1px, $color: inherit) {
	&:before {
		content: '';
		position: absolute;
		bottom: $bottom;
		left: $left;
		right: 0;
		height: $height;
		background-color: $color;
		width: $width;
		transform: scale(0,1);
		transform-origin: 100% 50%;
		transition: transform .4s cubic-bezier(.28,.38,0,.81);
	}
}

@mixin eltdfAnimationLineVisible($left: 0, $bottom:4px, $width: 100%, $height: 1px, $color: inherit) {
	&:before {
		content: '';
		position: absolute;
		bottom: $bottom;
		left: $left;
		right: 0;
		height: $height;
		background-color: $color;
		width: $width;
		transform: scale(1,1);
		transform-origin: 0 50%;
		transition: transform .4s cubic-bezier(.28,.38,0,.81);
	}
}

@mixin eltdfButtonTransparentColor() {
    color: $default-text-color;
    background-color: transparent;
	@include eltdfAnimationLineVisible($bottom: 2px);
}

@mixin eltdfButtonOutlineColor($important: null) {
    color: $first-main-color $important;
    background-color: transparent $important;
    border: 1px solid $first-main-color $important;
	@include eltdfAnimationLineVisible($left: -1px, $bottom:-5px, $width: calc(100% + 2px), $color:currentColor);
}

@mixin eltdfButtonOutlineHoverColor($important: null) {
	@include eltdfAnimationLineHidden($left: -1px, $bottom:-5px, $width: calc(100% + 2px), $color: currentColor)
}

@mixin eltdfButtonSolidColor($important: null) {
    color: #fff $important;
    background-color: $default-heading-color $important;
    border: 1px solid transparent $important;
	@include eltdfAnimationLineVisible($left: -1px, $bottom:-5px, $width: calc(100% + 2px), $color: inherit);
}

@mixin eltdfButtonSolidHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
	@include eltdfAnimationLineHidden($left: -1px, $bottom:-5px, $width: calc(100% + 2px), $color: inherit)
}

@mixin eltdfBurgerMenuAnim {
	overflow: hidden;
	
	line{
		transform: translateX(0);
		transform-origin: 0 50%;
		transition: transform .4s cubic-bezier(.28,.38,0,.81);
		
		&.eltdf-burger-filler {
			transform: translateX(-101%);
		}
		
		&:nth-child(1) {
			transition-delay: .4s;
		}
		&:nth-child(2) {
			transition-delay: .5s;
		}
		&:nth-child(3) {
			transition-delay: .6s;
		}
		&:nth-child(4) {
			transition-delay: .1s;
		}
		&:nth-child(5) {
			transition-delay: .2s;
		}
		&:nth-child(6) {
			transition-delay: .3s;
		}
	}
	
	&:hover {
		line {
			transform: translateX(101%);;
			
			&.eltdf-burger-filler {
				transform: translateX(0);
			}
			
			&:nth-child(1) {
				transition-delay: .1s;
			}
			&:nth-child(2) {
				transition-delay: .2s;
			}
			&:nth-child(3) {
				transition-delay: .3s;
			}
			&:nth-child(4) {
				transition-delay: .4s;
			}
			&:nth-child(5) {
				transition-delay: .5s;
			}
			&:nth-child(6) {
				transition-delay: .6s;
			}
		}
	}
}

@mixin eltdfPlaceholder() {
    &::-webkit-input-placeholder {
        @content
    }

    &:-moz-placeholder {
        @content;

        opacity: 1;
    }
    &::-moz-placeholder {
        @content;

        opacity: 1;
    }
    &:-ms-input-placeholder {
        @content
    }
}

// common mixins - end

