// mixins styles - start

%input-style {
    position: relative;
    width: 100%;
    margin: 0 0 $input-margin;
    padding: $input-vertical-padding $input-horizontal-padding;
    font-family: $default-text-font;
    font-size: 10px;
    font-weight: 400;
    text-transform: uppercase;
    letter-spacing: .16em;
    line-height: calc(#{$input-height} - (#{$input-vertical-padding} * 2) - 2px);
    color: $default-heading-color;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid $default-border-color;
    border-radius: 0;
    outline: 0;
    cursor: pointer;
    -webkit-appearance: none;
    box-sizing: border-box;
    @include eltdfTransition(border-color 0.2s ease-in-out);

    &:focus {
        color: $default-heading-color;
        border-color: $default-text-color;
    }

    @include eltdfPlaceholder {
        color: inherit;
        font-weight: 400;
		text-transform: uppercase;
    }
}

textarea{
    padding: 13px 25px !important;
    margin-top: 10px !important;
    font-size: 10px !important;;
    text-transform: none !important;
    letter-spacing: .16em !important;
    font-weight: 400 !important;
}

// mixins styles - end

