// heading mixins - start

@mixin eltdfDefaultHeadingStyle() {
    @include eltdfHeadingStyle();
    margin: 25px 0;
    -ms-word-wrap: break-word;
    word-wrap: break-word;
    
    a {
        @include eltdfTypographyLayout();
        
        &:hover {
            color: rgba($default-heading-color, 0.9);
        }
    }
}

@mixin eltdfHeadingStyle($with-heading: null) {
    font-weight: 600;
    font-family: $default-heading-font;
    
    @if ($with-heading == 'h1') {
        @include eltdfH1();
    } @else if ($with-heading == 'h2') {
        @include eltdfH2();
    } @else if ($with-heading == 'h3') {
        @include eltdfH3();
    } @else if ($with-heading == 'h4') {
        @include eltdfH4();
    } @else if ($with-heading == 'h5') {
        @include eltdfH5();
    } @else if ($with-heading == 'h6') {
        @include eltdfH6();
    }
}

@mixin eltdfH1() {
    font-size: 48px;
    line-height: 1.3em;
    color: $default-heading-color;
}

@mixin eltdfH2() {
    font-size: 38px;
    line-height: 1.1em;
    color: $default-heading-color;
}

@mixin eltdfH3() {
    font-size: 25px;
    line-height: 1.12em;
    color: $default-heading-color;
}

@mixin eltdfH4() {
    font-size: 18px;
    line-height: 1.33em;
    color: $default-heading-color;
}

@mixin eltdfH5() {
    font-size: 15px;
    line-height: 1.33em;
    color: $default-heading-color;
}

@mixin eltdfH6() {
    font-size: 14px;
    line-height: 1.8em;
    color: $default-heading-color;
}

// heading mixins - end

